
import {
  defineComponent,
  reactive,
  ref,
  toRefs,
  getCurrentInstance,
} from "vue";
import { useService } from "@hybridapi/service";
import { useTheme } from "/@/plugins/theme";
import { useI18n } from "vue-i18n";
import { useRouter, useRoute } from "/@/router";
import WordCloudChart from "./WordCloudChart.vue";

export default defineComponent({
  components: {
    WordCloudChart,
  },
  setup() {
    const tagKey = ref("frontend");
    const onChange = ({ key }) => {
      tagKey.value = key;
    };
    return {
      tagKey,
      onChange,
    };
  },
});
